\:root
  @each $color, $value in $colors
    --#{$color}: #{$value}

$priority-colors: ( "primary": $blue-800, "secondary": $gray-400, "success": $green-600, "info": $sky-500, "warning": $yellow-500, "danger": $red-800, "light": $gray-300, "dark": $black ) !default

\:root
  @each $color, $value in $priority-colors
    --#{$color}: #{$value}

$priority-colors-hover: ( "primary": $blue-900, "secondary": $gray-500, "success": $green-700, "info": $sky-600, "warning": $yellow-600, "danger": $red-900, "light": $gray-400, "dark": $neutral-600 ) !default

@each $color, $value in $priority-colors
  .bg-#{$color}, .btn.btn-#{$color}
    background-color: $value

@each $color, $value in $priority-colors-hover
  a.bg-#{$color}, .btn.btn-#{$color}
    &:hover
      background-color: darken($value, 10)

.bg-primary,
.bg-danger,
.bg-dark
  color: var(--white)

p, span
  color: var(--black)

.success
  color: var(--success)

.warning
  color: var(--warning)

.danger
  color: var(--danger)
